.body {
  justify-content: center;
  align-items: center;
  height: 100vmin;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 10px dotted #000;
  border-radius: 100%;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  margin: 80px auto;
  position: relative;
}

@keyframes movehand {
  from {
    transform: translateX(50%)rotate(0);
  }

  to {
    transform: translateX(50%)rotate(360deg);
  }
}

.hand, .minute-hand {
  transform-origin: bottom;
  background-color: #000;
  width: 10px;
  position: absolute;
  bottom: 50%;
  right: 50%;
}

.hand {
  height: 25vh;
  animation: 10s linear infinite movehand;
}

.minute-hand {
  height: 15vh;
  animation: 600s steps(60, end) infinite movehand;
}

.dot {
  background-color: #6495ed;
  border-radius: 100%;
  width: 25px;
  height: 25px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

